.btn {
  $this: &;
  display: inline-block;
  text-align: center;
  @include font-title;

  font-weight: 700;
  color: $white;
  padding: .75em 1em;
  position: relative;
  @include tr(color .3s, border-color .3s);
  @include before {
    position: absolute;
    z-index: -1;
    background-color: $accent;
    border: 1px solid $accent;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    transform: skew(-15deg);
    @include tr(transform .3s, background-color .3s, border-color .3s); }
  @include hover {
    &:not(#{$this}--no-hover) {
      color: $accent;
      @include before {
        background-color: transparent; } }
    // +before
 }    //   transform: skew(15deg)
  span {
    position: relative;
    z-index: 1; }
  @include xs {
    min-width: 0;
    width: 100%; }

  &--lg {
    font-size: em(21); }
  &--full {
    min-width: 0;
    width: 100%; }
  &--transp {
    color: $accent;
    @include before {
      background-color: transparent; }
    @include hover {
      &:not(#{$this}--no-hover) {
        color: $white;
        @include before {
          background-color: $accent; } } } } }
